<template>
  <main class="user__management__main">
    <UserPopup
      :popupEventFlag="popupEventFlag"
      @popupClose="popupToggle"
      :popupState="popupState"
      :selectedObj="selectedRowObj"
      :infoObj="infoObj"
      @refreshList="getUsers"
    />
    <section class="box__shadow__01 vertical__scroll grid__wrap">
      <h3 class="page__title">
        {{ this.$store.state.pageInfo["유저 관리"].title }}
      </h3>
      <h5 class="page__sub__title f__regular">
        {{ this.$store.state.pageInfo["유저 관리"].subTitle }}
      </h5>
      <div class="add__or__search__wrap">
        <ul>
          <li>
            <dl>
              <dt>기관</dt>
              <dd>
                <select
                  name=""
                  id=""
                  ref="orgnSelector"
                  v-model="orgnFilter"
                  @change="gridDataFiltering"
                >
                  <option value="">전체</option>
                  <option
                    v-for="orgn in orgnList"
                    :key="orgn.index"
                    :value="orgn.orgnId"
                  >
                    {{ orgn.orgnNm }}
                  </option>
                </select>
              </dd>
            </dl>
          </li>
          <li>
            <dl>
              <dt>사용여부</dt>
              <dd>
                <select
                  name=""
                  id=""
                  ref="useYnSelector"
                  v-model="useYnFilter"
                  @change="gridDataFiltering"
                >
                  <option value="">전체</option>
                  <option value="사용">사용</option>
                  <option value="미사용">미사용</option>
                </select>
              </dd>
            </dl>
          </li>
          <li>
            <dl>
              <dt>사용자명</dt>
              <dd>
                <input
                  type="text"
                  class="custom__input"
                  v-model="userNameFilter"
                />
              </dd>
            </dl>
          </li>
          <li>
            <button class="custom__btn" @click="gridDataFiltering">
              <span>
                <img
                  src="@/assets/images/icon/ic-search.svg"
                  alt="검색 아이콘"
                />
              </span>
              <p>검색</p>
            </button>
          </li>
        </ul>
        <button class="custom__btn" @click="popupToggle('추가')">
          <span>
            <img src="@/assets/images/icon/ic-plus.svg" alt="추가 아이콘" />
          </span>
          <p>추가</p>
        </button>
      </div>
      <div class="grid__wrap" @click="rowClick($event)">
        <Grid
          class="on"
          ref="gridEl"
          :cols="cols"
          :pagination="true"
          :sort="true"
          :rows="filteredGridData"
        ></Grid>
      </div>
    </section>
  </main>
</template>
<script>
import "@/assets/css/userManagement.css";
import { Grid } from "gridjs-vue";
import UserPopup from "@/components/SystemManagement/UserManagement/UserPopup";
import { getCommonCodeList } from "@/api/code";
import { getUserList } from "@/api/user";
import { getOrgnList } from "@/api/orgn";

export default {
  components: {
    Grid,
    UserPopup,
  },
  data() {
    return {
      popupEventFlag: "Close",
      cols: [
        { name: "번호", id: "index" },
        { name: "사용자ID", id: "userId" },
        { name: "이름", id: "userName" },
        { name: "연락처", id: "userHp" },
        { name: "성별", id: "userGender" },
        { name: "Email", id: "userEmail" },
        { name: "기관", id: "orgnNm" },
        { name: "진료과/부서", id: "codeName" },
        { name: "권한", id: "roleNm" },
        { name: "등록자", id: "registerName" },
        { name: "사용여부", id: "useYn" },
        { name: "등록일시", id: "createDate" },
      ],
      userList: [],
      orgnList: [],
      orgnFilter: "",
      useYnFilter: "사용",
      userNameFilter: "",
      selectedRowObj: "",
      popupState: "",
      infoObj: {},
      filteredGridDataBack: [],
      filteredGridData: [],
    };
  },
  computed: {
    getUserList() {
      return this.userList;
    },
  },
  created() {
    this.getOrgns();
    this.getDepts();
    this.getRoles();
    this.getCenterList();
    this.getUsers();
  },
  methods: {
    popupToggle(evtName) {
      if (evtName === "추가") this.selectedRowObj = false;
      this.popupState = evtName;
      this.popupEventFlag === "Close"
        ? (this.popupEventFlag = "Open")
        : (this.popupEventFlag = "Close");
    },
    rowClick(param) {
      const selectedObjIndex = Number(param.path[1].childNodes[0].textContent);
      const selectedCell = param.path[0].getAttribute("data-column-id");
      if (selectedCell === "userId") this.popupToggle("수정");
      if (this.$env == "development") console.log(this.userList);
      this.selectedRowObj = { ...this.userList[selectedObjIndex - 1] };
    },
    getUsers() {
      /*let filter = null;
      if (
        this.orgnFilter !== "" ||
        this.useYnFilter !== "" ||
        this.userNameFilter !== ""
      ) {
        filter = {
          orgnId: this.orgnFilter,
          useYn: this.useYnFilter,
          userName: this.userNameFilter,
        };
      }*/
      getUserList().then((response) => {
        let vm = this;
        vm.userList = response.data.response.data;
        vm.userList.forEach(function (object, index) {
          object.index = index + 1;
          if (object.useYn === "Y") {
            object.useYn = "사용";
          } else {
            object.useYn = "미사용";
          }
          if (object.userGender === "male") {
            object.userGender = "남성";
          } else {
            object.userGender = "여성";
          }
          if (
            vm.orgnList.filter((obj) => obj.orgnId === object.orgnId).length ==
            0
          ) {
            object.orgnNm = "미지정";
            object.orgnId = "";
          }
        });
        this.filteredGridDataBack = this.userList;
        if (
          this.orgnFilter === "" &&
          this.userNameFilter === "" &&
          this.useYnFilter === "사용"
        ) {
          this.filteredGridData = this.userList;
          this.filteredGridData = this.filteredGridData.filter(
            (obj) => obj.useYn.trim() === "사용"
          );
        } else {
          this.gridDataFiltering();
        }
      });
    },
    getOrgns() {
      getOrgnList().then((response) => {
        this.orgnList = response.data.response.data.filter(
          (obj) => obj.useYn == "Y"
        );
        this.infoObj.orgnList = this.orgnList;
      });
    },
    getDepts() {
      let searchQuery = "mainCommCode=DEPT&useYn=Y";
      getCommonCodeList(searchQuery).then((response) => {
        this.infoObj.deptList = response.data.response.codeList;
      });
    },
    getRoles() {
      let searchQuery = "mainCommCode=ROLE&useYn=Y";
      getCommonCodeList(searchQuery).then((response) => {
        this.infoObj.roleList = response.data.response.codeList;
      });
    },
    getCenterList() {
      let searchQuery = "mainCommCode=CNTR&useYn=Y";
      getCommonCodeList(searchQuery).then((response) => {
        this.infoObj.centerList = response.data.response.codeList;
      });
    },
    gridDataFiltering() {
      this.filteredGridData = this.filteredGridDataBack;

      if (this.orgnFilter !== "") {
        this.filteredGridData = this.filteredGridData.filter(
          (obj) => obj.orgnId.trim() === this.orgnFilter
        );
      }
      if (this.useYnFilter !== "") {
        this.filteredGridData = this.filteredGridData.filter(
          (obj) => obj.useYn.trim() === this.useYnFilter
        );
      }
      if (this.filteredGridData.length > 0) {
        const nameFilter = [];
        this.filteredGridData.forEach((obj) => {
          if (obj.userName.includes(this.userNameFilter)) {
            nameFilter.push(obj);
          }
        });
        this.filteredGridData = nameFilter;
      }
    },
  },
};
</script>
