<template>
  <div class="popup__wrap" :class="{ on: popupEventFlag === 'Open' }">
    <div class="popup__bg"></div>
    <div class="common__popup user_management_popup box__shadow__02">
      <div class="popup__top">
        <h4>사용자 {{ popupState }}</h4>
        <button class="pop__close__btn" @click="popupCloseClick"></button>
      </div>
      <ul>
        <li>
          <dl>
            <dt class="f__medium">사용자 ID</dt>
            <dd>
              <input
                type="text"
                class="custom__input"
                v-model="reqUserObj.userId"
                :disabled="popupState === '수정'"
              />
            </dd>
          </dl>
        </li>
        <li>
          <dl>
            <dt class="f__medium">Email</dt>
            <dd>
              <input
                type="email"
                class="custom__input"
                v-model="reqUserObj.userEmail"
              />
            </dd>
          </dl>
        </li>
        <li>
          <dl>
            <dt class="f__medium">이름</dt>
            <dd>
              <input
                type="text"
                class="custom__input"
                v-model="reqUserObj.userName"
              />
            </dd>
          </dl>
        </li>
        <li>
          <dl>
            <dt class="f__medium">기관</dt>
            <dd>
              <select
                name=""
                id=""
                ref="orgnSelector"
                v-model="reqUserObj.orgnId"
              >
                <option value="">미지정</option>
                <option
                  v-for="orgn in dataObj.orgnList"
                  :key="orgn.index"
                  :value="orgn.orgnId"
                >
                  {{ orgn.orgnNm }}
                </option>
              </select>
            </dd>
          </dl>
        </li>
        <li>
          <dl>
            <dt class="f__medium">비밀번호</dt>
            <dd>
              <input
                type="password"
                class="custom__input"
                v-model="reqUserObj.userPwd"
              />
            </dd>
          </dl>
        </li>
        <li v-if="false">
          <dl>
            <dt class="f__medium">센터</dt>
            <dd>
              <select name="" id="" v-model="reqUserObj.userCenter">
                <option value="">선택</option>
                <option
                  v-for="center in dataObj.centerList"
                  :key="center.index"
                  :value="center.codeId"
                >
                  {{ center.codeName }}
                </option>
              </select>
            </dd>
          </dl>
        </li>
        <li>
          <dl>
            <dt class="f__medium">비밀번호 확인</dt>
            <dd>
              <input
                type="password"
                class="custom__input"
                v-model="reqUserObj.userPwdCheck"
              />
            </dd>
          </dl>
        </li>
        <li>
          <dl>
            <dt class="f__medium">진료과/부서</dt>
            <dd>
              <select
                name=""
                id=""
                ref="orgnSelector"
                v-model="reqUserObj.deptId"
              >
                <option value="">선택</option>
                <option
                  v-for="dept in dataObj.deptList"
                  :key="dept.index"
                  :value="dept.codeId"
                >
                  {{ dept.codeName }}
                </option>
              </select>
            </dd>
          </dl>
        </li>
        <li>
          <dl>
            <dt class="f__medium">성별</dt>
            <dd>
              <select name="" id="" v-model="reqUserObj.userGender">
                <option value="">선택</option>
                <option value="male">남성</option>
                <option value="female">여성</option>
              </select>
            </dd>
          </dl>
        </li>
        <li>
          <dl>
            <dt class="f__medium">권한</dt>
            <dd>
              <select name="" id="" v-model="reqUserObj.roleId">
                <option value="">선택</option>
                <option
                  v-for="role in dataObj.roleList"
                  :key="role.index"
                  :value="role.codeId"
                >
                  {{ role.codeName }}
                </option>
              </select>
            </dd>
          </dl>
        </li>
        <li>
          <dl>
            <dt class="f__medium">연락처</dt>
            <dd>
              <input
                type="phone"
                class="custom__input"
                v-model="reqUserObj.userHp"
              />
            </dd>
          </dl>
        </li>
        <li>
          <dl>
            <dt class="f__medium">사용여부</dt>
            <dd>
              <select name="" id="" v-model="reqUserObj.useYn">
                <option value="">선택</option>
                <option value="Y">사용</option>
                <option value="N">미사용</option>
              </select>
            </dd>
          </dl>
        </li>
      </ul>
      <div class="custom__btn__wrap">
        <button class="custom__btn delete" @click="removeOrClean">
          <span>
            <img src="@/assets/images/icon/ic-remove.svg" alt="저장 아이콘" />
          </span>
          <p>삭제</p>
        </button>
        <button class="custom__btn" @click="addUser">
          <span>
            <img src="@/assets/images/icon/ic-save.svg" alt="저장 아이콘" />
          </span>
          <p>저장</p>
        </button>
      </div>
    </div>
  </div>
</template>
<script>
import { clear } from "@/utill/commom";
import { addUser, updateUser, removeUser } from "@/api/user";
import { checkPassword } from "@/utill/validator";

export default {
  props: {
    popupEventFlag: String,
    popupState: String,
    selectedObj: [Object, Boolean],
    infoObj: [Object, Boolean],
  },
  data() {
    return {
      reqUserObj: {
        userId: "",
        userPwd: "",
        userName: "",
        userHp: "",
        userGender: "",
        userEmail: "",
        orgnId: "",
        deptId: "",
        roleId: "",
        useYn: "",
        userCenter: "",
      },
      dataObj: {},
    };
  },
  watch: {
    popupState() {
      this.dataObj = this.infoObj;
      if (this.popupState === "수정") {
        this.reqUserObj = this.selectedObj;
        if (this.selectedObj.useYn === "사용") {
          this.reqUserObj.useYn = "Y";
        } else if (this.selectedObj.useYn === "미사용") {
          this.reqUserObj.useYn = "N";
        }
        if (this.selectedObj.userGender === "남성") {
          this.reqUserObj.userGender = "male";
        } else if (this.selectedObj.userGender === "여성") {
          this.reqUserObj.userGender = "female";
        }
      } else if (this.popupState === "추가") {
        clear(this.reqUserObj);
      }
    },
  },
  methods: {
    popupCloseClick() {
      this.$emit("popupClose", "Close");
    },
    addUser() {
      if (this.validateUser()) {
        if (this.$env == "development") console.log(this.reqUserObj);
        if (this.popupState === "수정") {
          updateUser(this.reqUserObj)
            .then((response) => {
              if (response.data.status === "OK") {
                this.$toast.success("사용자 수정이 완료되었습니다");
                this.popupCloseClick();
                this.$emit("refreshList");
              }
            })
            .catch((error) => this.$toast.error(error.message));
        } else {
          addUser(this.reqUserObj)
            .then((response) => {
              if (this.$env == "development") console.log(response);
              if (response.data.status === "OK") {
                this.$toast.success("사용자 추가가 완료되었습니다");
                this.popupCloseClick();
                this.$emit("refreshList");
              }
            })
            .catch((error) => this.$toast.error(error.message));
        }
      }
    },
    validateUser() {
      if (this.reqUserObj.userId === "" || this.reqUserObj.userId === null) {
        this.$toast.error("사용자 아아디를 입력하세요.");
        return false;
      }
      if (!checkPassword(this.reqUserObj.userPwd)) {
        this.$toast.error(
          "사용자 비밀번호는 6~20자 내 영문 대소문자, 숫자, 특수문자를 포함해야 합니다."
        );
        return false;
      }
      if (
        this.reqUserObj.userPwdCheck === "" ||
        this.reqUserObj.userPwdCheck === null
      ) {
        this.$toast.error("사용자 비밀번호를 다시 입력하세요.");
        return false;
      }
      if (this.reqUserObj.userPwd !== this.reqUserObj.userPwdCheck) {
        this.$toast.error("비밀번호를 일치하지 않습니다.");
        return false;
      }
      if (
        this.reqUserObj.userName === "" ||
        this.reqUserObj.userName === null
      ) {
        this.$toast.error("사용자 이름을 입력하세요.");
        return false;
      }
      if (this.reqUserObj.userHp === "" || this.reqUserObj.userHp === null) {
        this.$toast.error("연락처를 입력하세요.");
        return false;
      }
      if (
        this.reqUserObj.userGender === "" ||
        this.reqUserObj.userGender === null
      ) {
        this.$toast.error("성별을 입력하세요.");
        return false;
      }
      if (
        this.reqUserObj.userEmail === "" ||
        this.reqUserObj.userEmail === null
      ) {
        this.$toast.error("이메일을 입력하세요.");
        return false;
      }
      if (this.reqUserObj.orgnId === "" || this.reqUserObj.orgnId === null) {
        this.$toast.error("기관을 선택하세요.");
        return false;
      }
      if (this.reqUserObj.deptId === "" || this.reqUserObj.deptId === null) {
        this.$toast.error("진료과나 부서를 선택하세요.");
        return false;
      }
      if (this.reqUserObj.roleId === "" || this.reqUserObj.roleId === null) {
        this.$toast.error("권한을 선택하세요.");
        return false;
      }
      if (this.reqUserObj.useYn === "" || this.reqUserObj.useYn === null) {
        this.$toast.error("사용여부를 입력하세요.");
        return false;
      }
      return true;
    },
    removeOrClean() {
      if (this.popupState === "수정") {
        removeUser(this.reqUserObj.userId)
          .then((response) => {
            if (this.$env == "development") console.log(response);
            if (response.data.status === "OK") {
              this.$toast.success("사용자가 삭제되었습니다");
              this.popupCloseClick();
              this.$emit("refreshList");
            }
          })
          .catch((error) => this.$toast.error(error.message));
      } else if (this.popupState === "추가") {
        clear(this.reqUserObj);
      }
    },
  },
};
</script>
